body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Calibri', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: rgb(33, 33, 33);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App-h1 {
  font-family: 'Calibri';
  font-size: 2.5rem;
  letter-spacing: 2px;
  margin-bottom: 0;
}

.App-orange {
  
  color: orange;
}

.App-blue {
  color: rgb(18, 231, 231);
  text-shadow: 
    0 0 42px rgb(18, 231, 231),
    0 0 82px rgb(18, 231, 231),
    0 0 92px rgb(18, 231, 231),
    0 0 102px rgb(18, 231, 231),
    0 0 151px rgb(18, 231, 231);
}

.Board {
    
    display:flex;
    flex-direction: column;
    border-radius: 10px;
    padding: 2px;
   
    box-shadow: 
        0 0 7px #fff,
        0 0 10px #fff
        
}

.Board-row {
    display: inline-block; 
    width: auto;
}

.Board-hasWon {
    color: white;

    font-family: 'Calibri';
    font-size: 2.5rem;
    letter-spacing: 2px;
    margin-bottom: 0;
    padding: 0 20px;

    text-shadow: 
        0 0 20px white,
        0 0 30px white,
        0 0 40px white;
}
.Cell-b {
    width: 50px;
    height: 50px;

    border-radius: 50%;

    background-color: rgb(18, 231, 231);

    border: none;
    margin: 20px;

    box-shadow: 
    0 0 22px rgb(18, 231, 231),
    0 0 42px rgb(18, 231, 231)
    

}

.Cell-r {
    width: 50px;
    height: 50px;

    border-radius: 50%;

    background-color: rgb(231, 18, 18);

    border: none;
    margin: 20px;

    box-shadow: 
    0 0 22px rgb(231, 18, 18),
    0 0 42px rgb(231, 18, 18)
    
}


.options{   width: 50px;
    height: 50px;
    border-radius: 50%;
    
    border: none;
    margin: 20px;
    background-color:  rgba(135, 255, 71, 0.488);

    box-shadow: 
    0 0 22px  rgba(135, 255, 71, 0.488),
    0 0 42px  rgba(135, 255, 71, 0.488)
    

    
    
}

.Cell-off {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    
    border: none;
    margin: 20px;
    background-color: rgb(55, 55, 55);
}
